import React, { Component } from "react";
import JSONPretty from "react-json-pretty";
import Joi from "joi";
import styled, { ThemeProvider } from "styled-components";
import { theme } from "../themes";
import axios from "axios";
import { beUrl } from "../config";

const Container = styled.div`
  width: 80%;
  margin: 20px auto;

  & form {
    margin-bottom: 15px;
  }

  & pre {
    color: ${(props) => theme.primaryText};
  }

  & small {
    color: ${(props) => theme.badge.bg};
  }
`;

class CarForm extends Component {
  state = {
    car: {
      title: "",
      price: 0,
      showroom: false,
      sold: false,
      photos: "",
      attributes: {
        kms: { value: "" },
        year: { value: "" },
        month: { value: "" },
        brand: { value: "" },
        model: { value: "" },
        fuelType: { value: "" },
        motorCapacity: { value: "" },
        gearType: { value: "" },
        numberOfGears: { value: "" },
        hp: { value: "" },
        doors: { value: "" },
      },
    },
    result: "",
    errors: {},
  };

  async componentDidMount() {
    const { data } = await axios.get(`${beUrl}/cars`);

    const id = data.length + 1;

    const car = { ...this.state.car };
    car["id"] = id;

    this.setState({ car });
  }

  schema = Joi.object({
    id: Joi.number().required(),
    title: Joi.string().required(),
    price: Joi.number().greater(0),
    showroom: Joi.boolean(),
    sold: Joi.boolean(),
    photos: Joi.number().greater(0),
    attributes: Joi.object({
      kms: Joi.object({
        value: Joi.number().required(),
        description: Joi.string(),
      }),
      year: Joi.object({
        value: Joi.number().required().greater(0),
        description: Joi.string(),
      }),
      month: Joi.object({
        value: Joi.string().required(),
        description: Joi.string(),
      }),
      brand: Joi.object({
        value: Joi.string().required(),
        description: Joi.string(),
      }),
      model: Joi.object({
        value: Joi.string().required(),
        description: Joi.string(),
      }),
      fuelType: Joi.object({
        value: Joi.string().required(),
        description: Joi.string(),
      }),
      motorCapacity: Joi.object({
        value: Joi.number().required().greater(0),
        description: Joi.string(),
      }),
      gearType: Joi.object({
        value: Joi.string().required(),
        description: Joi.string(),
      }),
      numberOfGears: Joi.object({
        value: Joi.number().required(),
        description: Joi.string(),
      }),
      hp: Joi.object({
        value: Joi.number().required().greater(0),
        description: Joi.string(),
      }),
      doors: Joi.object({
        value: Joi.number().required().greater(0),
        description: Joi.string(),
      }),
    }),
  });

  handleChange = ({ currentTarget: input }) => {
    const car = { ...this.state.car };
    car[input.name] = input.value;
    this.setState({ car });
  };

  handleChangeAttributes = (attributeDesc, { currentTarget: input }) => {
    const car = { ...this.state.car };
    car.attributes[input.name] = {
      value: input.value,
      description: attributeDesc,
    };
    this.setState({ car });
  };

  validate = () => {
    const { error } = this.schema.validate(this.state.car, {
      abortEarly: false,
    });

    if (!error) return null;

    const errors = {};
    for (let item of error.details) {
      if (item.path[0] !== "attributes") errors[item.path[0]] = item.message;
      else {
        errors[item.path[0]] = errors[item.path[0]] ?? {};
        errors[item.path[0]][item.path[1]] =
          errors[item.path[0]][item.path[1]] ?? {};
        errors[item.path[0]][item.path[1]][item.path[2]] = "Inválido";
      }
    }
    return errors;
  };

  handleSubmit = async (e) => {
    e.preventDefault();
    let result;
    const errors = this.validate(this.state.car);

    if (errors) {
      result = { erro: "preencha o formulário corretamente" };
    } else {
      const { data } = await axios.post(`${beUrl}/cars`, this.state.car);
      result = data;
    }

    this.setState({ result, errors: errors || {} });
  };

  handleShowCars = async (e) => {
    e.preventDefault();

    const { data } = await axios.get(`${beUrl}/cars`);

    this.setState({ result: data });
  };

  render() {
    const { car, result, errors } = this.state;

    return (
      <ThemeProvider theme={this.props.theme}>
        <Container>
          <form>
            <div className="form-group">
              <label htmlFor="title">Título</label>
              <input
                value={car.title}
                onChange={this.handleChange}
                type="text"
                name="title"
                id="title"
                className="form-control"
              />
              {errors.title && <small>{errors.title}</small>}
            </div>
            <div className="form-group">
              <div className="form-row">
                <div className="col">
                  <label htmlFor="price">Preço</label>
                  <input
                    type="number"
                    value={car.price}
                    onChange={this.handleChange}
                    name="price"
                    id="price"
                    className="form-control"
                  />
                  {errors.price && <small>{errors.price}</small>}
                </div>
                <div className="col">
                  <label htmlFor="kms">Quilómetros</label>
                  <input
                    type="number"
                    value={car.attributes.kms.value}
                    onChange={(e) => this.handleChangeAttributes("KMs", e)}
                    name="kms"
                    id="kms"
                    className="form-control"
                  />
                  {errors.attributes?.kms?.value && (
                    <small>{errors.attributes.kms.value}</small>
                  )}
                </div>
              </div>
            </div>
            <div className="form-group">
              <div className="form-row">
                <div className="col">
                  <label htmlFor="year">Ano</label>
                  <input
                    type="number"
                    value={car.attributes.year.value}
                    onChange={(e) => this.handleChangeAttributes("Ano", e)}
                    name="year"
                    id="year"
                    className="form-control"
                  />
                  {errors.attributes?.year?.value && (
                    <small>{errors.attributes.year.value}</small>
                  )}
                </div>
                <div className="col">
                  <label htmlFor="month">Mês</label>
                  <input
                    type="text"
                    value={car.attributes.month.value}
                    onChange={(e) => this.handleChangeAttributes("Mês", e)}
                    name="month"
                    id="month"
                    className="form-control"
                  />
                  {errors.attributes?.month?.value && (
                    <small>{errors.attributes.month.value}</small>
                  )}
                </div>
              </div>
            </div>
            <div className="form-group">
              <div className="form-row">
                <div className="col">
                  <label htmlFor="brand">Marca</label>
                  <input
                    type="text"
                    value={car.attributes.brand.value}
                    onChange={(e) => this.handleChangeAttributes("Marca", e)}
                    name="brand"
                    id="brand"
                    className="form-control"
                  />
                  {errors.attributes?.brand?.value && (
                    <small>{errors.attributes.brand.value}</small>
                  )}
                </div>

                <div className="col">
                  <label htmlFor="model">Modelo</label>
                  <input
                    type="text"
                    value={car.attributes.model.value}
                    onChange={(e) => this.handleChangeAttributes("Modelo", e)}
                    name="model"
                    test="hhhh"
                    id="model"
                    className="form-control"
                  />
                  {errors.attributes?.model?.value && (
                    <small>{errors.attributes.model.value}</small>
                  )}
                </div>
              </div>
            </div>
            <div className="form-group">
              <div className="form-row">
                <div className="col">
                  <label htmlFor="fuelType">Combustível</label>
                  <input
                    type="text"
                    value={car.attributes.fuelType.value}
                    onChange={(e) =>
                      this.handleChangeAttributes("Combustível", e)
                    }
                    name="fuelType"
                    id="fuelType"
                    className="form-control"
                  />
                  {errors.attributes?.fuelType?.value && (
                    <small>{errors.attributes.fuelType.value}</small>
                  )}
                </div>
                <div className="col">
                  <label htmlFor="motorCapacity">Cilindrada</label>
                  <input
                    type="number"
                    value={car.attributes.motorCapacity.value}
                    onChange={(e) =>
                      this.handleChangeAttributes("Cilindrada", e)
                    }
                    name="motorCapacity"
                    id="motorCapacity"
                    className="form-control"
                  />
                  {errors.attributes?.motorCapacity?.value && (
                    <small>{errors.attributes.motorCapacity.value}</small>
                  )}
                </div>

                <div className="col">
                  <label htmlFor="hp">Cavalos</label>
                  <input
                    type="number"
                    value={car.attributes.hp.value}
                    onChange={(e) => this.handleChangeAttributes("Cavalos", e)}
                    name="hp"
                    id="hp"
                    className="form-control"
                  />
                  {errors.attributes?.hp?.value && (
                    <small>{errors.attributes.hp.value}</small>
                  )}
                </div>
              </div>
            </div>
            <div className="form-group">
              <label htmlFor="doors">Portas</label>
              <input
                type="number"
                value={car.attributes.doors.value}
                onChange={(e) => this.handleChangeAttributes("Portas", e)}
                name="doors"
                id="doors"
                className="form-control"
              />
              {errors.attributes?.doors?.value && (
                <small>{errors.attributes.doors.value}</small>
              )}
            </div>
            <div className="form-group">
              <div className="form-row">
                <div className="col">
                  <label htmlFor="gearType">Tipo de Mudanças</label>
                  <select
                    value={car.attributes.gearType.value}
                    onChange={(e) => this.handleChangeAttributes("Mudanças", e)}
                    name="gearType"
                    className="form-control"
                    id="gearType"
                  >
                    <option value=""></option>
                    <option value="automático">Automático</option>
                    <option value="manual">Manual</option>
                  </select>
                  {errors.attributes?.gearType?.value && (
                    <small>{errors.attributes.gearType.value}</small>
                  )}
                </div>
                <div className="col">
                  <label htmlFor="numberOfGears">Número de Mudanças</label>
                  <input
                    type="number"
                    value={car.attributes.numberOfGears.value}
                    onChange={(e) =>
                      this.handleChangeAttributes("# Mudanças", e)
                    }
                    name="numberOfGears"
                    id="numberOfGears"
                    className="form-control"
                  />
                  {errors.attributes?.numberOfGears?.value && (
                    <small>{errors.attributes.numberOfGears.value}</small>
                  )}
                </div>
              </div>
            </div>
            <div className="form-group">
              <div className="form-row">
                <div className="col">
                  <label htmlFor="photos">Número de Fotos</label>
                  <input
                    type="number"
                    value={car.photos}
                    onChange={this.handleChange}
                    name="photos"
                    id="photos"
                    className="form-control"
                  />
                  {errors.photos && <small>{errors.photos}</small>}
                </div>
                <div className="col">
                  <label htmlFor="sold">Vendido</label>
                  <select
                    value={car.sold}
                    onChange={this.handleChange}
                    name="sold"
                    className="form-control"
                    id="sold"
                  >
                    <option value="false">Não</option>
                    <option value="true">Sim</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="form-group"></div>
            <button className="btn btn-primary" onClick={this.handleSubmit}>
              Gerar Código
            </button>
            <button
              className="btn btn-primary ml-2"
              onClick={this.handleShowCars}
            >
              Mostrar Existentes
            </button>
          </form>

          <JSONPretty id="json-pretty" data={result}></JSONPretty>
        </Container>
      </ThemeProvider>
    );
  }
}

export default CarForm;
