import React, { Component, Suspense } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import styled, { ThemeProvider } from "styled-components";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "./common/loading";
import Helmet from "react-helmet";
import { ToastContainer, toast } from "react-toastify";
import { formatKms } from "./common/helper";
import { beUrl } from "../config";

const ContactForm = React.lazy(() => import("./common/contact-form"));

const FullscreenToggle = styled.span`
  position: fixed;
  z-index: 999999999999;
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  font-size: 30px;
  color: white;
  padding: 5px 13px;
  border-radius: 50%;
  text-align: center;
  background-color: ${(props) => props.theme.badge.bg};
  border: 4px solid transparent;

  &:hover {
    border: 4px solid orange;
  }
`;

const SliderContainer = styled.div`
  & img {
    width: inherit;
    background-image: url("/img/pulse.gif");
    background-repeat: no-repeat;
    background-position: center;
  }
`;

const DetailsList = styled.ul`
  list-style-type: none;
  padding-left: 0px;
  color: ${(props) => props.theme.primaryText};
`;

const Price = styled.span`
  background-color: ${(props) => props.theme.badge.bg};
  color: ${(props) => props.theme.badge.fg};
`;

const H1 = styled.h1`
  color: ${(props) => props.theme.primaryText};
`;

const Error404 = styled.div`
  margin: 50px auto;
  color: ${(props) => props.theme.bgColor};
  -webkit-filter: invert(100%);
  filter: invert(100%);
  font-size: 40px;
  text-align: center;

  & span {
    font-size: 1.4em;
    border-bottom: 3px solid red;
  }
`;

class CarDetails extends Component {
  state = { fullscreen: false };

  constructor(props) {
    super(props);

    this.primaryRef = React.createRef();
    this.secundaryRef = React.createRef();

    this.toggleFullscreen = this.toggleFullscreen.bind(this);
  }

  renderSlides(car) {
    const items = [];

    for (let i = 1; i <= car.photos; i++) {
      items.push(
        <SplideSlide key={`${car.id}/${i}`}>
          <img
            src={process.env.PUBLIC_URL + `/img/${car.id}/${i}.webp`}
            alt={"Fotos da viatura"}
          />
        </SplideSlide>
      );
    }

    return items;
  }

  getSlider(car, primaryOptions, secondaryOptions) {
    return (
      <SliderContainer>
        <Splide options={primaryOptions} ref={this.primaryRef}>
          {this.renderSlides(car)}
        </Splide>
        <Splide
          options={secondaryOptions}
          ref={this.secundaryRef}
          className="mt-3"
        >
          {this.renderSlides(car)}
        </Splide>
      </SliderContainer>
    );
  }

  getBadge(car) {
    return car.sold ? (
      <h1>
        <span className="badge badge-warning">Vendido</span>
      </h1>
    ) : (
      <h1>
        <Price className="badge badge-primary">{car.price}€</Price>
      </h1>
    );
  }

  toggleFullscreen() {
    const { fullscreen } = this.state;
    var aux = !fullscreen;
    this.setState({ fullscreen: aux });
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.primaryRef.current !== null) {
      this.primaryRef.current.sync(this.secundaryRef.current.splide);
    }
  }

  async componentDidMount() {
    // Set the sync target right after the component is mounted.
    if (this.primaryRef.current !== null) {
      this.primaryRef.current.sync(this.secundaryRef.current.splide);
    }

    try {
      let { data: car } = await axios.get(
        `${beUrl}/cars/${this.props.match.params.id}`
      );

      if (!car) {
        car = -1;
      } else {
        document.title = car.title;
      }

      this.setState({ car });
    } catch {
      this.setState({ car: -1 });
    }
  }

  GetCarSpecs(car) {
    return (
      <table className="table table-hover">
        <tbody>
          {Object.keys(car.attributes).map((key) => {
            return (
              <tr key={key}>
                <th scope="row">{car.attributes[key].description}</th>
                <td>
                  {key === "kms"
                    ? formatKms(car.attributes[key].value)
                    : car.attributes[key].value}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    );
  }

  structuredJSON = (car) => {
    let data = {
      "@context": "http://schema.org/",
      "@type": "Product",
      name: `${car.title}`,
      image: `https://vinculobjetivo.pt/img/${car.id}/thumbnail.webp`,
      description: `${car.title} | ${car.attributes.kms.value} KMS | ${car.attributes.year.value} | ${car.attributes.fuelType.value}`,
      url: `https://vinculobjetivo.pt/carros`,
      offers: {
        "@type": "Offer",
        priceCurrency: "€",
        price: car.price,
        url: `https://vinculobjetivo.pt/carro/${car.id}`,
        itemCondition: "https://schema.org/UsedCondition",
        availability: `${
          car.sold ? "https://schema.org/SoldOut" : "https://schema.org/InStock"
        }`,
        seller: {
          "@type": "Organization",
          name: "Vinculobjetivo",
        },
      },
      brand: {
        "@type": "Brand",
        name: `${car.attributes.brand.value}`,
      },
    };

    return JSON.stringify(data);
  };

  render() {
    const primaryOptions = {
      type: "loop",
      perMove: 1,
      gap: "1rem",
      pagination: true,
      lazyLoad: "nearby",
    };

    const secondaryOptions = {
      type: "slide",
      rewind: true,
      gap: "1rem",
      pagination: false,
      fixedWidth: 110,
      fixedHeight: 70,
      cover: true,
      focus: "center",
      isNavigation: true,
      updateOnMove: true,
    };

    const { car, fullscreen } = this.state;
    const { id } = this.props.match.params;

    if (!car) {
      return (
        <LoadingContainer>
          <LoadingSpinner></LoadingSpinner>
        </LoadingContainer>
      );
    }

    if (car === -1) {
      return (
        <ThemeProvider theme={this.props.theme}>
          <Error404>
            <span>Erro:</span> ID da viatura não existe: {id}
          </Error404>
        </ThemeProvider>
      );
    }

    if (car && car.sold) {
      var options = {
        position: "top-right",
        autoClose: false,
        hideProgressBar: true,
        closeOnClick: false,
        pauseOnHover: false,
        draggable: false,
        progress: undefined,
      };

      toast.error("⚠️ Esta viatura já foi vendida 😢", options);
    }

    return (
      <Suspense
        fallback={
          <LoadingContainer>
            <LoadingSpinner></LoadingSpinner>
          </LoadingContainer>
        }
      >
        <ToastContainer limit={1} />
        <ThemeProvider theme={this.props.theme}>
          <Helmet>
            <script className="structured-data-list" type="application/ld+json">
              {this.structuredJSON(car)}
            </script>
          </Helmet>

          <FullscreenToggle>
            <i
              className={fullscreen ? "fas fa-compress" : "fas fa-expand"}
              onClick={this.toggleFullscreen}
            ></i>
          </FullscreenToggle>
          {!fullscreen ? (
            <div>
              <div className="row ml-lg-5 mr-lg-5 ml-sm-2 mr-sm-2 ml-1 mr-1">
                <div className="col-12 col-lg-9 mt-5">
                  {this.getSlider(car, primaryOptions, secondaryOptions)}
                </div>
                <div className="col-12 col-lg-3 mt-5">
                  <H1>{car.title}</H1>
                  {this.getBadge(car)}
                  <DetailsList>{this.GetCarSpecs(car)}</DetailsList>
                </div>
              </div>
              <ContactForm
                theme={this.props.theme}
                subject={car.title}
              ></ContactForm>
            </div>
          ) : (
            this.getSlider(car, primaryOptions, secondaryOptions)
          )}
        </ThemeProvider>
      </Suspense>
    );
  }
}

export default CarDetails;
