import React, { Component, Suspense } from "react";
import LoadingSpinner, { LoadingContainer } from "./common/loading";

const ContactUs = React.lazy(() => import("./contactUs"));
const Services = React.lazy(() => import("./services"));
const Showroom = React.lazy(() => import("./showroom"));

class Home extends Component {
  state = {};
  render() {
    return (
      <Suspense
        fallback={
          <LoadingContainer>
            <LoadingSpinner></LoadingSpinner>
          </LoadingContainer>
        }
      >
        <Services theme={this.props.theme.services} />
        <Showroom
          theme={this.props.theme.showroom}
          history={this.props.history}
        />
        <ContactUs theme={this.props.theme.contactUs} />
      </Suspense>
    );
  }
}

export default Home;
